import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('./views/Home.vue')
    },
    {
      path: '/tx/:txHash',
      name: 'tx',
      component: () => import('./views/Tx.vue')
    }
  ]
})
